:root {
  /* Light Mode (Default) - Modern Color Scheme */
  --bg-primary: #FAFAFF;
  --bg-surface: #FFFFFF;
  --bg-card: #FFFFFF;
  --bg-elevated: #F8F7FF;
  --bg-hover: #F0EEFF;
  --bg-active: #E8E4FF;

  --border-primary: #E5E1F0;
  --border-secondary: #D8D2ED;
  --border-accent: #C4B5FD;

  --text-primary: #1E1B4B;
  --text-secondary: #3B3856;
  --text-muted: #524E6E;
  --text-disabled: #8A85A3;

  /* Primary Purple/Violet Palette */
  --purple-50: #FAF5FF;
  --purple-100: #F3E8FF;
  --purple-200: #E9D5FF;
  --purple-300: #D8B4FE;
  --purple-400: #C084FC;
  --purple-500: #A855F7;
  --purple-600: #9333EA;
  --purple-700: #7C3AED;
  --purple-800: #6B21A8;
  --purple-900: #581C87;

  /* Secondary Violet Palette */
  --violet-400: #A78BFA;
  --violet-500: #8B5CF6;
  --violet-600: #7C3AED;
  --violet-700: #6D28D9;

  /* Accent Blue-Purple (Indigo) */
  --indigo-400: #818CF8;
  --indigo-500: #6366F1;
  --indigo-600: #4F46E5;

  /* Glass Effects - Light */
  --glass-bg: rgba(255, 255, 255, 0.9);
  --glass-border: rgba(139, 92, 246, 0.2);
  --glass-highlight: rgba(168, 85, 247, 0.08);

  /* Gradient Stops */
  --gradient-start: #A855F7;
  --gradient-mid: #8B5CF6;
  --gradient-end: #6366F1;

  /* CTA - Light */
  --cta-primary-bg: #7C3AED;
  --cta-primary-text: #FFFFFF;
  --cta-primary-hover: #6D28D9;
  --cta-secondary-bg: transparent;
  --cta-secondary-text: #1E1B4B;
  --cta-secondary-border: #DDD6FE;

  /* Neon Glow - Light */
  --glow-purple: rgba(124, 58, 237, 0.25);
  --glow-violet: rgba(109, 40, 217, 0.2);

  /* Accent Colors */
  --accent-purple: #A855F7;
  --accent-violet: #8B5CF6;
  --accent-indigo: #6366F1;
  --accent-emerald: #10B981;
  --accent-turquoise: #14B8A6;
  --accent-amber: #F59E0B;
  --accent-rose: #F43F5E;
}

.dark {
  /* Dark Mode - Based on #271b3d - Enhanced Color Scheme */
  --bg-primary: #271b3d;
  --bg-surface: #2d1f45;
  --bg-card: #33214d;
  --bg-elevated: #3a2555;
  --bg-hover: #41255d;
