  --border-primary: #4a2f6d;
  --border-secondary: #55377d;
  --border-accent: #603f8d;

  --text-primary: #FFFFFF;
  --text-secondary: #F0F0FF;
  --text-muted: #D1D1EB;
  --text-disabled: #A1A1C3;

  /* Glass Effects - Dark */
  --glass-bg: rgba(39, 27, 61, 0.9);
  --glass-border: rgba(139, 92, 246, 0.2);
