  --glow-purple: rgba(168, 85, 247, 0.4);
  --glow-violet: rgba(139, 92, 246, 0.3);

  /* Accent Colors - Dark Mode (same as light for consistency) */
  --accent-emerald: #10B981;
  --accent-turquoise: #14B8A6;
  --accent-amber: #F59E0B;
  --accent-rose: #F43F5E;
}

/* Utility Classes */

/* Structural Foundation - Prevent Layout Breaking */
html,
body {
  overflow-x: hidden;
  max-width: 100vw;
  position: relative;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  /* Global Grid Background - Seamless, 5% opacity */
  background-color: var(--bg-primary);
  background-image:
    linear-gradient(rgba(139, 92, 246, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(139, 92, 246, 0.03) 1px, transparent 1px);
  background-size: 40px 40px;
  background-position: 0 0, 0 0;
  background-attachment: fixed;
  background-repeat: repeat;
}

/* Light Mode Grid Background - 5% opacity */
body:not(.dark) {
  background-color: var(--bg-primary);
  background-image:
    linear-gradient(rgba(139, 92, 246, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(139, 92, 246, 0.03) 1px, transparent 1px);
  background-size: 40px 40px;
  background-position: 0 0, 0 0;
  background-attachment: fixed;
  background-repeat: repeat;
}

/* Dark Mode Global Background with Grid - 5% opacity */
.dark body {
  background-color: var(--bg-primary);
  background-image:
    linear-gradient(rgba(139, 92, 246, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(139, 92, 246, 0.03) 1px, transparent 1px);
  background-size: 40px 40px;
  background-position: 0 0, 0 0;
  background-attachment: fixed;
  background-repeat: repeat;
}

/* iOS Safari viewport height fix */
@supports (-webkit-touch-callout: none) {
  .min-h-screen {
    min-height: -webkit-fill-available;
  }
  
  html {
    height: -webkit-fill-available;
  }
}

/* Dark mode color variables for hardcoded backgrounds */
.dark {
  --bg-sidebar: #171717;
  --bg-header: #2a2a2a;
}

:root {
  --bg-sidebar: #f5f5f5;
  --bg-header: #ffffff;
}

/* Ensure all sections are properly contained */
section {
  position: relative;
  width: 100%;
  max-width: 100%;
  z-index: 1;
  display: block;
  overflow-x: hidden;
  contain: layout style;
}

/* Prevent absolute children from breaking layout */
section>* {
  max-width: 100%;
}

/* Ensure product interface demo is properly contained */
.isolate {
  isolation: isolate;
  position: relative;
  overflow: hidden;
}

/* Ensure content appears above grid background */
main {
  position: relative;
  z-index: 1;
  background-color: transparent;
  width: 100%;
  overflow-x: hidden;
}

/* Subtle section divider for clear boundaries */
.section-divider {
  height: 1px;
  width: 100%;
  opacity: 0.5;
  background: linear-gradient(to right,
      transparent,
      rgba(148, 163, 184, 0.35),
      transparent);
}

/* Hide section dividers in dark mode */
.dark .section-divider {
  display: none !important;
  opacity: 0 !important;
  visibility: hidden !important;
}

/* Hide all section borders in dark mode */
.dark section {
  border-top: none !important;
  border-bottom: none !important;
}

/* Hide border-y in dark mode */
.dark .border-y {
  border-top: none !important;
  border-bottom: none !important;
}

/* Ensure seamless background between sections in dark mode */
.dark section[id="platform"],
.dark div[id="security"] {
  border-top: none !important;
  border-bottom: none !important;
}

/* Only make sections with explicit bg-primary transparent to show grid */
/* Use utility class bg-transparent in JSX instead */

/* Ensure proper stacking - Prevent layout conflicts */
nav {
  position: fixed;
  z-index: 50;
  top: 0;
  left: 0;
  right: 0;
}

footer {
  position: relative;
  z-index: 10;
  background-color: var(--bg-surface);
}

/* Prevent content from breaking out */
* {
  box-sizing: border-box;
}

/* Animation Classes for Interactive Hero */
@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fade-in-up {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scale-up {
  from {
    opacity: 0;
    transform: scale(0.95);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes slide-in-right {
  from {
    opacity: 0;
    transform: translateX(20px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.animate-fade-in {
  animation: fade-in 0.3s ease-out forwards;
}

.animate-fade-in-up {
  animation: fade-in-up 0.4s ease-out forwards;
}

.animate-scale-up {
  animation: scale-up 0.2s ease-out forwards;
}

.animate-slide-in-right {
  animation: slide-in-right 0.3s ease-out forwards;
}

.animate-pulse-slow {
  animation: pulse 3s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Respect reduced motion preference */
@media (prefers-reduced-motion: reduce) {

  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }

  .animate-fade-in,
  .animate-fade-in-up,
  .animate-scale-up,
  .animate-slide-in-right,
  .animate-pulse-slow {
    animation: none !important;
  }
}

