   RESPONSIVE TYPOGRAPHY & FLUID DESIGN
   ======================================== */

/* Fluid Typography Scale using clamp() */
:root {
  /* Base font size scales from 14px (mobile) to 16px (desktop) */
  --font-size-xs: clamp(0.75rem, 0.7rem + 0.25vw, 0.875rem);
  --font-size-sm: clamp(0.8125rem, 0.75rem + 0.3125vw, 0.9375rem);
  --font-size-base: clamp(0.875rem, 0.8rem + 0.375vw, 1rem);
  --font-size-lg: clamp(1rem, 0.9rem + 0.5vw, 1.125rem);
  --font-size-xl: clamp(1.125rem, 1rem + 0.625vw, 1.25rem);
  --font-size-2xl: clamp(1.25rem, 1.1rem + 0.75vw, 1.5rem);
  --font-size-3xl: clamp(1.5rem, 1.2rem + 1.5vw, 1.875rem);
  --font-size-4xl: clamp(1.875rem, 1.4rem + 2.375vw, 2.25rem);
  --font-size-5xl: clamp(2.25rem, 1.6rem + 3.25vw, 3rem);
  --font-size-6xl: clamp(2.75rem, 1.8rem + 4.75vw, 3.75rem);
  --font-size-7xl: clamp(3rem, 2rem + 5vw, 4.5rem);
  
  /* Fluid spacing */
  --space-xs: clamp(0.25rem, 0.2rem + 0.25vw, 0.5rem);
  --space-sm: clamp(0.5rem, 0.4rem + 0.5vw, 0.75rem);
  --space-md: clamp(1rem, 0.8rem + 1vw, 1.5rem);
  --space-lg: clamp(1.5rem, 1.2rem + 1.5vw, 2rem);
  --space-xl: clamp(2rem, 1.5rem + 2.5vw, 3rem);
  --space-2xl: clamp(3rem, 2rem + 5vw, 5rem);
  --space-3xl: clamp(4rem, 2.5rem + 7.5vw, 8rem);
}

/* Touch-friendly minimum tap target size */
.touch-target {
  min-height: 44px;
  min-width: 44px;
}

/* Focus states for accessibility */
*:focus-visible {
  outline: 2px solid var(--accent-violet);
  outline-offset: 2px;
  border-radius: 4px;
}

/* Skip to main content link for accessibility */
.skip-link {
  position: absolute;
  top: -100%;
  left: 0;
  padding: 1rem;
  background: var(--bg-primary);
  color: var(--text-primary);
  z-index: 9999;
  transition: top 0.3s;
}

.skip-link:focus {
  top: 0;
}

/* ========================================
   RESPONSIVE BREAKPOINT UTILITIES
   ======================================== */

/* Extra small devices (320px and up) */
@media (min-width: 320px) {
  .container-responsive {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

/* Small devices (375px and up) */
@media (min-width: 375px) {
  .container-responsive {
    padding-left: 1.25rem;
    padding-right: 1.25rem;
  }
}

/* Medium phones (414px and up) */
@media (min-width: 414px) {
  .container-responsive {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

/* Tablets (768px and up) */
@media (min-width: 768px) {
  .container-responsive {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

/* Laptops (1024px and up) */
@media (min-width: 1024px) {
  .container-responsive {
    padding-left: 2.5rem;
    padding-right: 2.5rem;
  }
}

/* Large screens (1280px and up) */
@media (min-width: 1280px) {
  .container-responsive {
    padding-left: 3rem;
    padding-right: 3rem;
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;
  }
}

/* Extra large screens (1440px and up) */
@media (min-width: 1440px) {
  .container-responsive {
    max-width: 1400px;
  }
}

/* ========================================
   RESPONSIVE IMAGES & MEDIA
   ======================================== */

img,
video,
picture,
svg {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Prevent images from overflowing containers */
.img-responsive {
  width: 100%;
  height: auto;
  -o-object-fit: cover;
     object-fit: cover;
}

/* Aspect ratio containers */
.aspect-video {
  aspect-ratio: 16 / 9;
}

.aspect-square {
  aspect-ratio: 1 / 1;
}

/* ========================================
   MOBILE-FIRST BUTTON & FORM STYLES
   ======================================== */

/* Touch-friendly buttons */
button,
.btn,
[role="button"] {
  min-height: 44px;
  padding: 0.75rem 1.5rem;
  cursor: pointer;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
}

/* Form inputs - touch friendly */
input,
textarea,
select {
  min-height: 44px;
  padding: 0.75rem 1rem;
  font-size: 16px; /* Prevents zoom on iOS */
  border-radius: 0.5rem;
  border: 1px solid var(--border-primary);
  background: var(--bg-card);
  color: var(--text-primary);
  width: 100%;
  touch-action: manipulation;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
  border-color: var(--accent-violet);
  box-shadow: 0 0 0 3px rgba(139, 92, 246, 0.2);
}

/* ========================================
   GRADIENT TEXT UTILITY
   ======================================== */

.gradient-text {
  background: linear-gradient(135deg, var(--purple-500), var(--violet-500), var(--indigo-500));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* ========================================
   PREVENT LAYOUT SHIFT (CLS)
   ======================================== */

/* Reserve space for images */
.img-placeholder {
  background: var(--bg-elevated);
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

/* Fixed aspect ratio containers to prevent CLS */
.ratio-16-9 {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%;
}

.ratio-4-3 {
  position: relative;
  width: 100%;
  padding-bottom: 75%;
}

.ratio-1-1 {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
}

.ratio-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

/* ========================================
   RESPONSIVE GRID UTILITIES
   ======================================== */

.grid-responsive {
  display: grid;
  gap: var(--space-md);
  grid-template-columns: 1fr;
}

@media (min-width: 640px) {
  .grid-responsive {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .grid-responsive {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1280px) {
  .grid-responsive {
    grid-template-columns: repeat(4, 1fr);
  }
}

